@import 'fonts.less';
@import 'settings.less';
@import 'smart-grid.less';

.reset();

html {
    font-size: 10px;
}

body {
    font-family: @Geometria;
}

.wrapper {
    .wrapper();
}

.header {
    background: url('/img/home_animalshelter_slider_pic1.png') no-repeat;
    background-position: left;
}

.btn {
    box-shadow: 0 0 20px rgba(133, 126, 224, 0.21);
    border-radius: 20px;
    background: @orange;
}

.subtitle {
    color: @orange-2;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 15px;
}

.title {
    color: #000;
    font-size: 3rem;
    font-weight: 300;
    padding-bottom: 25px;

    strong {
        font-weight: 700;
    }
}

.main-block {
    margin-top: 50px;

}

.container-header {
    padding-top: 20px;
    display: flex;
    .justify-content(space-between);
    .justify-content-xs(center);
    align-items: center;
    flex-wrap: wrap;
}

.logo {
    display: flex;
    .justify-content-xs(center);
    align-items: center;
    .size(5);
    .size-xs(12);
    .col();

    .logo-img {
        font-size: 2.4rem;
        font-weight: 700;
        color: #fff;
        margin-right: 30px;
        background: @orange;
        padding: 25px 20px;
    }

    .logo-text {
        .logo-title {
            color: @black;
            font-weight: 600;
            font-size: 2rem;
        }

        .logo-subtitle {
            color: @black;
            font-weight: 400;
            font-size: 1.4rem;
        }
    }
}

.call {
    .size(5);
    .size-md(7);
    .size-xs(12);
    .col();
    display: flex;
    justify-content: flex-end;
    .justify-content-xs(center);
    flex-wrap: wrap;

    .number {
        font-size: 1.8rem;
        font-weight: 600;
        margin-right: 15px;
    }

    .btn-call {
        width: 165px;
        height: 40px;
        color: #fff;
        font-size: 1.4rem;
        font-weight: 400;
        padding: 15px 25px;
        text-decoration: none;
    }
}

.container-descr {
    padding-top: 130px;
    display: flex;
    justify-content: space-between;
    .justify-content(center);
    flex-wrap: wrap;

}

.main-block-text {
    .size(6);
    .size-md(12);
    .col;

    .main-title {
        font-size: 4.8rem;
        color: @black;
        font-weight: 700;
        line-height: 60px;
        margin-bottom: 35px;

        .orange {
            color: @orange;
        }
    }

    .main-subtitle {
        font-size: 1.6rem;
        color: @black;
        font-weight: 400;
        line-height: 24px;
    }

    .button {
        margin-top: 95px;

        .btn-try {
            width: 263px;
            height: 60px;
            border-radius: 30px;
            padding: 22px 35px;
            color: #fff;
            font-weight: 700;
            font-size: 1.6rem;
            text-decoration: none;
        }
    }

}

.tagline {
    .size(6);
    .size-md(10);
    .offset-md(1);
    .size-sm(12);
    .offset-sm(0);
    .col();
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;



    .tagline-text {
        .size(6);
        .size-xs(12);
        .col();
        position: absolute;
        right: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        .tagline-slogan {
            color: @black;
            font-size: 1.7rem;
            line-height: 30px;
            font-weight: 500;

        }

        .tagline-autor {
            font-size: 1.6rem;
            color: @orange;
            font-weight: 400;
        }
    }
}



.exersices {
    padding-top: 150px;
    background: url(/img/bg-2nd-block.png) no-repeat;
    background-position: right;
}


.exersices-descr {
    .size(10);
    .size-md(12);




    .exersices-descr {
        color: #000000;
        font-size: 1.6rem;
        font-weight: 400;
    }
}


.exersices-items {
    .row-flex();
    justify-content: space-between;
    padding-top: 100px;

    .exersices-item {
        .size(5);
        .size-sm(12);
        .col();
        display: flex;
        padding-bottom: 100px;
        align-items: flex-end;

        .text-wrap {
            padding-left: 20px;
        }

        .exersices-title {
            color: @orange-2;
            font-size: 1.8rem;
            font-weight: 700;
            padding-bottom: 15px;
        }

        .exersices-subtitle {
            color: #000;
            font-weight: 400;
            font-size: 1.4rem;
        }
    }
}

.button-lesson {
    text-align: center;

    .btn-lesson {
        background: #f69625;
        border: none;
        border-radius: 30px;
        box-shadow: 0 17px 30px rgba(146, 27, 57, 0.22);
        color: #fff;
        font-size: 1.6rem;
        font-weight: 700;
        padding: 20px 37px;
        cursor: pointer;
    }
}

.for-who {
    background: url(/img/partenrs-bg.png) no-repeat;
    background-position: center;
    overflow: hidden;
}

.who-descr {
    padding-top: 140px;
}

.who-items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.who-item {
    .size(5);
    .size-sm(12);
    display: flex;
    align-items: baseline;

    .fa-check {
        font-size: 1.6rem;
        color: #f69625;
        padding-right: 20px;
    }

    .who-item-descr {
        font-size: 2rem;
        padding-bottom: 40px;
        font-weight: 500;
        color: #000000;

    }
}

.pricing {
    margin-top: 60px;
    /*
    background: url(/img/bg-boat.jpg) no-repeat;
    */
    background-position: center;
    background-size: 300%;

}

.pricing-items {
    .row-flex();
    justify-content: center;


    .pricing-item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .size(3);
        .size-md(6);
        .size-sm(10);
        .col();
        box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
        border-radius: 20px;
        text-align: center;
        background: #fff;
    }


}

.wrap-text {

    .pricing-name {
        font-size: 1.8rem;
        font-weight: 700;
        text-transform: uppercase;
        padding-top: 35px;
        padding-bottom: 20px;
    }

    small {
        font-size: 1.4rem;
        font-weight: 500;
    }

    .separator {
        width: 50px;
        height: 2px;
        background-color: #f69625;
        margin: 0 auto;
    }

    .price {
        font-size: 2.4rem;
        font-weight: 700;
        text-transform: uppercase;
        padding: 40px 0;
    }
}

.pricing-point {

    margin: 0 30px 30px 30px;

    .point-text {
        font-size: 1.6rem;
        font-weight: 400;
    }
}

.wrap-button {

    .button-register {
        width: 200px;
        height: 50px;
        box-shadow: 0 10px 15px rgba(191, 4, 4, 0.15);
        border-radius: 25px;
        border: none;
        background-color: #f69625;
        color: #fff;
        font-size: 1.4rem;
        font-weight: 400;
        margin: 40px 0;
        cursor: pointer;
    }
}

.features {
    margin-top: 100px;
    margin-bottom: 150px;
}

.features-items {
    .row-flex();

}

.features-item {
    display: flex;
    .size(6);
    .size-md(12);
    .col();
    margin-bottom: 65px;


    img {
        height: 165px;
        width: 165px;
        margin-right: 30px;
    }

    .features-text {
        .size-xs(12);
    }

    .features-title {
        font-size: 2rem;
        font-weight: 700;

    }

    .separator-features {
        width: 50px;
        height: 2px;
        background-color: #f69625;
        margin-top: 15px;
        margin-bottom: 20px;

    }

    .features-info {
        font-size: 1.6rem;
        font-weight: 400;
    }
}

.about-hero {
    background-color: #fdf2f2;
    position: relative;
    overflow: hidden;
}

.hero-descr {
    padding-top: 100px;

    .hero-name {
        padding-bottom: 50px;
        font-size: 1.8rem;
        font-weight: 400;
    }
}


.hero-photo {
    position: absolute;
    margin-top: 75px;
    margin-right: 40px;
    width: 500px;
    right: 0;
    top: 0;
}


.qualities-item {
    .size(5);
    .size-md(11);
    .offset-left(1);
    display: flex;
    align-items: baseline;
    padding-bottom: 30px;

    .qualities-descr {
        font-size: 1.4rem;
        font-weight: 600;
        margin-left: 22px;
    }
}

.contact{
    background: url(/img/bg-contacts.png) no-repeat;
}

.adress-wrap {
    .size(6);
    .size-md(12);
}

.adress-props {
    padding-top: 45px;
    .size(9);
}

.props-item {

    display: flex;
    align-items: center;
    margin-bottom: 35px;

    img {
        margin-right: 20px;
    }

    .style-text {
        font-size: 1.8rem;
        font-weight: 700;
        display: inline-block;
    }


}

.wrap-flex-contact {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 100px;
}


.map iframe {
    height: 200px;
    .size(12);
    border-radius: 10px;
    box-shadow: 0 10px 15px rgba(191, 4, 4, 0.1);
}


.feedback {
    .size(6);
    .size-md(12);
    
}

.feedback-form {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    

    & input {
        width: 95%;
        border: none;
        border-bottom: 1px solid #ff5959;
        color: #000000;
        font-size: 1.6rem;
        font-weight: 400;
        letter-spacing: 0.4px;
        padding-bottom: 15px;
        padding-left: 40px;
        margin-bottom: 50px;
    }

    & button{
        padding: 24px 80px 20px 80px;
        background: #f69625;
        border-radius: 30px;
        border: none;
        color: #fff;
        box-shadow: 0 24px 29px 3px rgba(105, 48, 48, 0.12);
        font-size: 1.8rem;
        font-weight: 700;
        align-self: flex-end;
        cursor: pointer;
        
    }
}

.feedback-title {
    font-size: 3rem;
    font-weight: 400;
    letter-spacing: 0.75px;

    & strong {
        font-weight: 700;
    }
}


.footer{
    background: #fdf2f2;
    margin-top: 100px;
}

.footer-wrap{
    padding-top: 70px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 60px;
}

.left-side {
    .size(4);
    .size-xs(12);
    p{
        padding-bottom: 20px;
    }
}

.to-up{
    color: #f69625;
    font-size: 1.8rem;
    font-weight: 700;
    text-decoration: none;
}

.red-text{
    color: @orange;
    font-size: 1.6rem;
    font-weight: 400;
    text-decoration: none;
}

.copyright {
    padding-top: 70px;

    p{
        font-size: 1.4rem;
        font-weight: 400;
        color: #000;
    }
    
}

.right-side{
    .size(4);
    .size-sm(6);
    .size-xs(12);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.social{
    img{
        height: 32px;
        width: 32px;
        padding-left: 35px;
    }
}

.footer-number{
    padding-top: 45px;
    font-size: 2rem;
    font-weight: 700;
    padding-bottom: 10px;
}

.footer-button{
    background: #f1852b;
    border: none;
    border-radius: 20px;
    color: #fff;
    padding: 15px 38px;
}

.footer-mail{
    font-size: 2rem;
    font-weight: 700;
    padding-top: 40px;
}

.right-side-links{
    padding-top: 110px;    
    P{
        text-align: right;
        padding-bottom: 20px;
        
    }
}
@media screen and (max-width: 992px) {
    .tagline {
        margin-top: 90px;
    }

    .pricing-item {
        margin-bottom: 20px;
    }

    .hero-photo {
        position: inherit;
        margin: 0 auto;
    }

    .feedback{
        margin-top: 60px;
    }

    .feedback-form{
        & button{
            align-self: center;
        }
    }
}

@media screen and (max-width: 720px) {
    .number {
        margin-bottom: 20px;
    }

    .exersices-descr {
        text-align: center;
    }

    .exersices-item {
        align-items: center;
    }


}



@media screen and (max-width: 576px) {
    .call {
        margin-top: 30px;
    }

    .tagline {
        justify-content: center;

        .tagline-text {
            margin-bottom: 40px;
            position: inherit;
            order: -1;
            text-align: center;
        }
    }

    .main-title {
        text-align: center;
    }

    .main-subtitle {
        text-align: center;
    }

    .button {
        text-align: center;
    }

    .features-item {
        flex-wrap: wrap;
    }

    .features-text {
        padding-top: 20px;
        text-align: center;
    }

    .separator-features {
        margin: 0 auto;
    }

    .features-item {
        justify-content: center;

        img {
            margin-right: 0;
        }
    }

    .hero-photo {
        width: 350px;

    }

    .feedback-form {
        & input{
            width: 87%;
        }
    }
    
    .footer-wrap{
        justify-content: center;
        text-align: center;
    }

    .right-side{
        align-items: center;
    }

    .right-side-links p{
        text-align: center;
    }

    .social {
        padding-right: 35px;
    }
}
